@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  --spacing: 12px 0;

  --border-radius: 8px;

  --medium-large-text: 30px;
  --medium-text: 20px;
  --subtitle-text: 24px;
  --body-text: 16px;

  --light-pink: #ffecec;
  --dark-pink: #f697cd;
  --orange: #ce8c4f;
  --beige: #fff8eb;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #ffecec;
  background-color: var(--light-pink);
  font-family: "Raleway", sans-serif;
  margin: 0;
}

h1,
h2 {
  color: #f697cd;
  color: var(--dark-pink);
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 40px;
  margin: 12px 0;
  margin: var(--spacing);
}

h2 {
  font-size: 30px;
  font-size: var(--medium-large-text);
  margin: 12px 0;
  margin: var(--spacing);
}

h3 {
  font-size: 20px;
  font-size: var(--medium-text);
  color: #000;
}

p {
  font-size: 16px;
  font-size: var(--body-text);
  margin: 12px 0;
  margin: var(--spacing);
  text-align: center;
}

button {
  border-radius: 8px;
  border-radius: var(--border-radius);
  border: 1px solid #000;
  background-color: #ce8c4f;
  background-color: var(--orange);
  color: #fff;
  margin: 12px;
  cursor: pointer;
}

.beige-area {
  background-color: #fff8eb;
  background-color: var(--beige);
  width: 100%;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  margin: 24px 0;
  padding: 24px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.basket-open {
  overflow: hidden;
}

@media (max-width: 640px) {
  :root {
    --spacing: 12px 0;
    --medium-large-text: 1.5em;
    --subtitle-text: 1.2em;
    --medium-text: 1em;
    --body-text: 12px;
  }
}

.donut-hero {
  width: 20em;
  margin: var(--spacing);
}

.subtitle {
  font-size: var(--subtitle-text);
  margin: var(--spacing);
  text-align: center;
  width: 80%;
}

.cta {
  font-size: var(--medium-text);
  padding: 12px 70px;
}

.locations {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 24px 0;
}

.product-card {
  width: 30%;
  flex-grow: 0;
  background-color: var(--light-pink);
  border: 1px solid #000;
  border-radius: var(--border-radius);
}

.product-card img {
  border-radius: var(--border-radius) var(--border-radius) 0 0;
}

.location-info {
  padding: 12px;
}

.large-black-text {
  font-size: var(--medium-large-text);
  font-weight: 900;
}

img {
  width: 100%;
}

@media /* screen and */ (max-width: 1000px) {
  .locations {
    flex-wrap: wrap;
    align-content: space-between;
  }

  .product-card {
    width: 100%;
    margin: var(--spacing);
  }
}

@media (max-width: 640px) {
  .donut-hero {
    width: 10em;
  }
  h1 {
    font-size: 2em;
  }
}

.basket {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  z-index: 1;
  display: flex;
}

.overlay {
  background-color: #000;
  opacity: 0.7;
  width: 70%;
}

.basket-overview {
  width: 30%;
  height: 100%;
  background-color: var(--light-pink);
  position: relative;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  display: flex;
  padding-top: 30px;
}

.basket-overview h1 {
  padding-top: 80px;
}

.close-button {
  padding: 4px;
  display: flex;
  position: absolute;
  right: 0;
}

.fa-times {
  font-size: 18px;
  line-height: 1;
}

.basket-card {
  display: flex;
  flex-direction: row;
  grid-gap: 20px;
  gap: 20px;
  align-items: center;
  flex-wrap: wrap;
  width: 70%;
}

.donut-info {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 200px;
}
.donut-quantity {
  width: 100%;
}

.basket-card p {
  margin: unset;
  text-align: left;
}

.donut-item.basket-card .donut-image img {
  width: 100px;
  height: 100px;
}

.subtract,
.add {
  border-radius: 100%;
  background: var(--beige);
  width: 30px;
  height: 30px;
  border: 1px solid #000;
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
}

.fa-minus,
.fa-plus {
  font-size: 12px;
}

.donut-quantity-controls {
  width: 100%;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  grid-gap: 20px;
  gap: 20px;
  align-self: center;
  justify-content: center;
}

.quantity-input {
  width: 50px;
  border: 1px solid #000;
  border-radius: var(--border-radius);
  background: var(--beige);
  text-align: center;
}

.trash {
  display: flex;
  justify-content: center;
  align-items: center;
}

nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.hamburger-icon {
  display: none;
}

.donut-logo {
  width: 6em;
  margin-top: 40px;
  margin-left: 40px;
}

.nav-tabs {
  width: 30%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.tab {
  position: relative;
  cursor: pointer;
}

.tab:hover {
  text-decoration: underline;
}

.tab a {
  color: #000;
  text-decoration: none;
}

.basket-nav {
  position: relative;
}

.basket-quantity {
  padding: 2px 6px;
  background-color: #f697cd;
  position: absolute;
  top: -6px;
  right: 16px;
  border-radius: 3px;
  text-align: center;
  color: #000;
}

.shopping-basket {
  font-size: 28px;
}

/* Footer styles */

footer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  position: absolute;
}

@media (max-width: 640px) {
  nav {
    margin: 5%;
  }
  .donut-logo {
    width: 3em;
    margin: 0;
  }

  /* hamburger menu */
  .hamburger-icon {
    display: block;
    z-index: 1;
  }
  .nav-tabs {
    display: none;
  }
  .hamburger-menu {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: var(--light-pink);
    position: absolute;
    top: 0px;
    margin: -5%;
    text-align: center;
    padding: 60px 0 20px;
  }

  .tab {
    margin: 2% 0;
  }
  .shopping-basket {
    font-size: 20px;
  }
  footer {
    flex-direction: column;
  }
}

/* Menu page styles */

.product-grid {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  max-width: 1000px;
  margin: 24px 0;
  padding: 48px 0;
  grid-gap: 80px;
  gap: 80px;
}

.donut-item {
  margin: 0 0 24px 0;
}

.donut-image img {
  width: 120px;
  height: 120px;
}

.quantity-selector {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 60%;
}

.quantity {
  font-size: var(--medium-text);
}

.donut-name {
  font-size: var(--medium-text);
}
.donut-price {
  font-size: var(--medium-text);
  margin: 0;
}

.small-button {
  font-size: var(--body-text);
  padding: 8px 20px;
  margin: 12px;
}

.donuts-added {
  background-color: var(--dark-pink);
  color: #000;
  width: 45%;
  text-align: center;
  border-radius: var(--border-radius);
  padding: 1%;
  /* transition: all 2s ease-out; */
  opacity: 80%;
  font-size: var(--body-text);
}

